        },
        getCategory() {
            this.aaa = this.$route.params;
        },
        async getData(category) {
            const res = await fetch("https://jsonplaceholder.typicode.com/posts");
            // const res = await fetch("https://btktech.com.tw/polyacetal/api001");
            // const res1 = await fetch(`https://btktech.com.tw/polyacetal/${category}`);
            const finalRes = await res.json();
            this.listItems = finalRes;
        },
    },
    mounted() {
        this.getData(this.getActId());
        // this.getData()
        this.getCategory()
        console.log('mounted:', this.aaa)
    },
    setup() {

    },
